/* Quiz Page - Orbit Style */
.quiz_wrapper {
  min-height: 100vh;
  background: linear-gradient(180deg, #0d0d1a 0%, #1a1a2e 50%, #0d0d1a 100%);
}

.quiz_wrapper .content {
  padding: 100px 24px 48px;
  max-width: 800px;
  margin: 0 auto;
}

/* Quiz Header */
.quiz_wrapper .quiz-header {
  text-align: center;
  margin-bottom: 32px;
}

.quiz_wrapper .quiz-header h1 {
  font-size: 24px;
  font-weight: 700;
  color: white;
  margin-bottom: 8px;
}

.quiz_wrapper .quiz-header p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.6);
}

/* Question Card */
.question {
  padding: 24px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  margin-top: 20px;
  line-height: 1.5;
  transition: all 0.3s ease;
}

.question:hover {
  background: rgba(255, 255, 255, 0.05);
}

.question h3 {
  color: white;
  font-weight: 500;
  font-size: 15px;
  margin-bottom: 20px;
  line-height: 1.6;
}

.question h3 span {
  color: #a855f7;
  font-weight: 700;
}

/* Options */
.question .options {
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  display: flex;
  gap: 8px;
}

.option {
  position: relative;
  width: 100%;
}

.option label {
  line-height: 1.5;
  display: block;
  width: 100%;
  text-align: left;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  padding: 14px 20px;
  padding-right: 48px;
  border-radius: 10px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  transition: all 0.3s ease;
}

.option label:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(168, 85, 247, 0.3);
}

.option input[type="radio"] {
  display: none;
}

/* Correct Answer */
.attempted.correct ~ label {
  border: 1px solid #10b981;
  color: #10b981;
  background: rgba(16, 185, 129, 0.1);
}

/* Wrong Answer */
input[type="radio"]:checked.wrong ~ label {
  border: 1px solid #ef4444;
  color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

/* Option Icons */
.optionIcon {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  display: none;
  width: 24px;
  height: 24px;
}

input[type="radio"]:checked ~ .optionIcon {
  display: block;
}

/* Reason */
.reason {
  font-size: 13px;
  margin-top: 16px;
  padding: 16px;
  background: rgba(16, 185, 129, 0.1);
  border-radius: 10px;
  color: #10b981;
  line-height: 1.6;
  border-left: 3px solid #10b981;
}

/* Submit Section */
.quiz_wrapper .submit {
  margin-top: 32px;
  margin-bottom: 48px;
  display: flex;
  justify-content: center;
  width: 100%;
}

.quiz_wrapper .SubmitButton {
  font-size: 15px;
  font-weight: 600;
  padding: 14px 40px;
  border-radius: 10px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 24px rgba(168, 85, 247, 0.3);
}

.quiz_wrapper .SubmitButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 32px rgba(168, 85, 247, 0.4);
}

/* Responsive */
@media (max-width: 768px) {
  .quiz_wrapper .content {
    padding: 80px 16px 32px;
  }

  .question {
    padding: 20px;
  }

  .question h3 {
    font-size: 14px;
  }

  .option label {
    font-size: 13px;
    padding: 12px 16px;
    padding-right: 40px;
  }

  .optionIcon {
    width: 20px;
    height: 20px;
    right: 12px;
  }

  .reason {
    font-size: 12px;
    padding: 12px;
  }
}
