@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow: auto;
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  max-width: 100vw;
  font-family: Inter, sans-serif;
  background: linear-gradient(180deg, #0d0d1a 0%, #1a1a2e 50%, #0d0d1a 100%);
  color: white;
}

html {
  height: 100%;
  overflow-x: hidden;
}

#root {
  width: 100%;
  height: 100%;
}

::selection {
  background-color: #a855f7;
  color: white;
}

.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 12px;
}

header {
  width: 100%;
  height: 56px;
  line-height: 56px;
  padding: 8px 16px;
}

h1 {
  font-size: clamp(28px, 5vw, 48px);
  font-weight: 700;
}

h2 {
  font-size: clamp(18px, 3vw, 24px);
  font-weight: 500;
}

h3 {
  font-size: clamp(14px, 2vw, 18px);
  font-weight: 500;
}

p {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.6;
}

.inputContainer {
  max-width: 90%;
}

input,
select,
option,
textarea {
  font-size: 16px;
  font-weight: 500;
  width: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  color: white;
}

input:focus,
select:focus,
textarea:focus {
  outline: none;
}

option {
  background-color: #1a1a2e;
  color: #a5a5a5;
  font-size: 14px;
}

::placeholder {
  color: rgba(107, 107, 125, 0.7);
}

button {
  cursor: pointer;
  outline: none;
  border: none;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.05);
}

::-webkit-scrollbar-thumb {
  background: rgba(168, 85, 247, 0.3);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(168, 85, 247, 0.5);
}
