/* Orbit-Style Design */
.orbit-page {
  min-height: 100vh;
  background: linear-gradient(180deg, #0d0d1a 0%, #1a1a2e 50%, #0d0d1a 100%);
  overflow-x: hidden;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 24px;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 8px;
}

.logo-icon {
  font-size: 24px;
  color: #0d0d1a;
}

.logo-text {
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 4px;
  color: #0d0d1a;
}

.nav-links {
  display: flex;
  gap: 32px;
}

.nav-links a {
  color: #4a4a5a;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #0d0d1a;
}

.nav-buttons {
  display: flex;
  gap: 12px;
  align-items: center;
}

.nav-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s;
}

.nav-btn.secondary {
  background: transparent;
  border: 1px solid #ddd;
  color: #4a4a5a;
}

.nav-btn.secondary:hover {
  border-color: #0d0d1a;
  color: #0d0d1a;
}

.nav-btn.primary {
  background: #0d0d1a;
  border: none;
  color: white;
}

.nav-btn.primary:hover {
  background: #1a1a2e;
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: #0d0d1a;
}

/* Hero Section */
.hero {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 120px 24px 80px;
  overflow: hidden;
}

.hero-bg {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.orbit-line {
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  left: 50%;
  bottom: -50%;
  transform: translateX(-50%);
}

.orbit-1 {
  width: 800px;
  height: 800px;
}

.orbit-2 {
  width: 1200px;
  height: 1200px;
}

.orbit-3 {
  width: 1600px;
  height: 1600px;
}

.planet {
  position: absolute;
  border-radius: 50%;
  background: linear-gradient(135deg, #2a2a4a 0%, #1a1a2e 100%);
  box-shadow: inset -10px -10px 30px rgba(0, 0, 0, 0.5), 0 0 20px rgba(168, 85, 247, 0.2);
}

.planet-1 {
  width: 120px;
  height: 120px;
  left: 10%;
  bottom: 20%;
}

.planet-2 {
  width: 80px;
  height: 80px;
  right: 15%;
  bottom: 25%;
}

.planet-3 {
  width: 60px;
  height: 60px;
  right: 30%;
  bottom: 15%;
}

.planet-sun {
  width: 40px;
  height: 40px;
  left: 50%;
  bottom: 10%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, #f59e0b 0%, #fbbf24 100%);
  box-shadow: 0 0 40px rgba(245, 158, 11, 0.5);
}

.hero-content {
  position: relative;
  z-index: 10;
  text-align: center;
  max-width: 800px;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: rgba(255, 255, 255, 0.95);
  padding: 8px 20px;
  border-radius: 50px;
  font-size: 12px;
  font-weight: 600;
  color: #4a4a5a;
  margin-bottom: 24px;
}

.hero-badge svg {
  color: #a855f7;
}

.hero-title {
  font-size: clamp(32px, 5vw, 56px);
  font-weight: 700;
  line-height: 1.2;
  color: white;
  margin-bottom: 20px;
}

.gradient-text {
  background: linear-gradient(135deg, #c084fc 0%, #a855f7 50%, #7c3aed 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-style: italic;
}

.hero-subtitle {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.6;
  margin-bottom: 32px;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.hero-buttons {
  display: flex;
  gap: 16px;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 48px;
}

.hero-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 14px 28px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.hero-btn.primary {
  background: #a855f7;
  border: none;
  color: white;
  box-shadow: 0 10px 30px rgba(168, 85, 247, 0.3);
}

.hero-btn.primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(168, 85, 247, 0.4);
}

.hero-btn.secondary {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
}

.hero-btn.secondary:hover {
  background: rgba(255, 255, 255, 0.15);
}

.hero-stats {
  display: flex;
  gap: 48px;
  justify-content: center;
  flex-wrap: wrap;
}

.stat {
  text-align: center;
}

.stat-num {
  display: block;
  font-size: 28px;
  font-weight: 700;
  color: white;
}

.stat-label {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
}

/* Section Styles */
.section-header {
  text-align: center;
  margin-bottom: 48px;
}

.section-badge {
  display: inline-block;
  background: rgba(168, 85, 247, 0.1);
  color: #a855f7;
  padding: 6px 16px;
  border-radius: 50px;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 16px;
}

.section-header h2 {
  font-size: clamp(24px, 4vw, 36px);
  font-weight: 700;
  color: white;
  margin-bottom: 12px;
}

.section-header p {
  font-size: 15px;
  color: rgba(255, 255, 255, 0.6);
}

/* Features Section */
.features-section {
  padding: 80px 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 24px;
}

.feature-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 28px;
  transition: all 0.3s;
}

.feature-card:hover {
  background: rgba(255, 255, 255, 0.06);
  transform: translateY(-4px);
}

.feature-card .feature-icon {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, rgba(168, 85, 247, 0.2) 0%, rgba(99, 102, 241, 0.2) 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a855f7;
  margin-bottom: 16px;
}

.feature-card h3 {
  font-size: 16px;
  font-weight: 600;
  color: white;
  margin-bottom: 8px;
}

.feature-card p {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.5;
}

/* How It Works */
.how-section {
  padding: 80px 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.steps-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 24px;
}

.step-card {
  position: relative;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 28px;
  text-align: center;
}

.step-number {
  display: block;
  font-size: 32px;
  font-weight: 800;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 16px;
}

.step-card h3 {
  font-size: 15px;
  font-weight: 600;
  color: white;
  margin-bottom: 8px;
}

.step-card p {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.6);
}

.step-arrow {
  display: none;
}

/* Courses Section */
.courses-section {
  padding: 80px 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.course-card {
  display: flex;
  align-items: center;
  gap: 16px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-left: 3px solid var(--accent);
  padding: 20px;
  border-radius: 12px;
  text-decoration: none;
  transition: all 0.3s;
}

.course-card:hover {
  background: rgba(255, 255, 255, 0.06);
  transform: translateX(4px);
}

.course-icon {
  width: 48px;
  height: 48px;
  background: var(--accent);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  flex-shrink: 0;
}

.course-info {
  flex: 1;
}

.course-info h3 {
  font-size: 15px;
  font-weight: 600;
  color: white;
  margin-bottom: 4px;
}

.course-time {
  display: block;
  font-size: 12px;
  color: var(--accent);
}

.course-level {
  font-size: 11px;
  color: rgba(255, 255, 255, 0.5);
}

.course-arrow {
  color: rgba(255, 255, 255, 0.3);
}

/* Testimonials */
.testimonials-section {
  padding: 80px 24px;
  max-width: 1200px;
  margin: 0 auto;
}

.testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 24px;
}

.testimonial-card {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  padding: 24px;
}

.testimonial-stars {
  display: flex;
  gap: 4px;
  margin-bottom: 16px;
}

.testimonial-card > p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  margin-bottom: 20px;
  font-style: italic;
}

.testimonial-author {
  display: flex;
  align-items: center;
  gap: 12px;
}

.author-avatar {
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 600;
  font-size: 14px;
}

.testimonial-author h4 {
  font-size: 14px;
  font-weight: 600;
  color: white;
  margin-bottom: 2px;
}

.testimonial-author span {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.5);
}

/* CTA Section */
.cta-section {
  padding: 80px 24px;
  text-align: center;
}

.cta-content {
  max-width: 600px;
  margin: 0 auto;
}

.cta-content h2 {
  font-size: clamp(24px, 4vw, 32px);
  font-weight: 700;
  color: white;
  margin-bottom: 16px;
}

.cta-content p {
  font-size: 15px;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 32px;
}

.cta-btn {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 16px 32px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 10px 30px rgba(168, 85, 247, 0.3);
}

.cta-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(168, 85, 247, 0.4);
}

/* Footer */
.footer {
  background: rgba(0, 0, 0, 0.3);
  padding: 60px 24px 24px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 40px;
  margin-bottom: 40px;
}

.footer-brand {
  max-width: 250px;
}

.footer-brand .nav-logo .logo-icon,
.footer-brand .nav-logo .logo-text {
  color: white;
}

.footer-brand p {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 12px;
}

.footer-links {
  display: flex;
  gap: 60px;
  flex-wrap: wrap;
}

.footer-col h4 {
  font-size: 13px;
  font-weight: 600;
  color: white;
  margin-bottom: 16px;
}

.footer-col a {
  display: block;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  margin-bottom: 10px;
  transition: color 0.3s;
}

.footer-col a:hover {
  color: white;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 24px;
  text-align: center;
}

.footer-bottom p {
  font-size: 12px;
  color: rgba(255, 255, 255, 0.4);
}

/* Auth Modal */
.auth-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 24px;
}

.auth-modal {
  background: white;
  border-radius: 20px;
  padding: 40px;
  width: 100%;
  max-width: 400px;
  position: relative;
}

.auth-close {
  position: absolute;
  top: 16px;
  right: 16px;
  background: none;
  border: none;
  color: #999;
  cursor: pointer;
}

.auth-modal-header {
  text-align: center;
  margin-bottom: 32px;
}

.auth-modal-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  margin: 0 auto 16px;
}

.auth-modal-header h2 {
  font-size: 22px;
  font-weight: 700;
  color: #0d0d1a;
  margin-bottom: 6px;
}

.auth-modal-header p {
  font-size: 14px;
  color: #666;
}

.auth-modal-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.auth-modal-form input {
  width: 100%;
  padding: 14px 16px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 14px;
  color: #0d0d1a;
  background: #f9f9f9;
}

.auth-modal-form input:focus {
  outline: none;
  border-color: #a855f7;
  background: white;
}

.auth-modal-error {
  background: #fee;
  border: 1px solid #fcc;
  color: #c00;
  padding: 10px;
  border-radius: 8px;
  font-size: 13px;
  text-align: center;
}

.auth-modal-submit {
  width: 100%;
  padding: 14px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border: none;
  border-radius: 10px;
  color: white;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.auth-modal-submit:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 30px rgba(168, 85, 247, 0.3);
}

.auth-modal-switch {
  text-align: center;
  margin-top: 24px;
}

.auth-modal-switch p {
  font-size: 13px;
  color: #666;
}

.auth-modal-switch button {
  background: none;
  border: none;
  color: #a855f7;
  font-weight: 600;
  cursor: pointer;
}

/* Responsive */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: white;
    flex-direction: column;
    padding: 20px;
    gap: 16px;
    border-bottom: 1px solid #eee;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-buttons {
    display: none;
  }

  .mobile-menu-btn {
    display: block;
  }

  .hero {
    padding: 100px 20px 60px;
  }

  .hero-title {
    font-size: 28px;
  }

  .hero-stats {
    gap: 24px;
  }

  .stat-num {
    font-size: 22px;
  }

  .features-grid,
  .steps-grid,
  .courses-grid,
  .testimonials-grid {
    grid-template-columns: 1fr;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-links {
    gap: 32px;
  }

  .auth-modal {
    padding: 24px;
  }
}

/* Hide old styles */
.profile_wrapper .info,
.newTopic,
.courses,
.progress {
  display: none;
}
