/* Roadmap Page - Orbit Style */
.roadmap_wrapper {
  min-height: 100vh;
  background: linear-gradient(180deg, #0d0d1a 0%, #1a1a2e 50%, #0d0d1a 100%);
}

.roadmap_wrapper .content {
  padding: 100px 24px 48px;
  max-width: 1000px;
  margin: 0 auto;
}

.roadmap_wrapper .topic {
  justify-content: space-between;
  margin-bottom: 24px;
}

/* Topic Bar */
.topic-bar {
  position: relative;
  padding: 24px;
  padding-top: 40px;
  margin-block: 16px;
  z-index: 0;
  border-left: 4px solid var(--clr);
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-left: 4px solid var(--clr);
  transition: all 0.3s ease;
}

.topic-bar:hover {
  background: rgba(255, 255, 255, 0.05);
}

.topic-bar .topic-bar-title {
  padding-left: 16px;
}

.topic-bar .week {
  font-size: 12px;
  font-weight: 600;
  color: var(--clr);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 4px;
}

.topic-bar h2 {
  font-size: 18px;
  font-weight: 600;
  color: white;
}

.plus {
  position: absolute;
  right: 20px;
  top: 28px;
  background-color: transparent;
  border: none;
  border-radius: 50%;
  padding: 0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.plus svg {
  width: 32px;
  height: 32px;
}

/* Subtopics */
.subtopics {
  transition: all 0.3s ease;
  overflow: hidden;
  margin-top: 20px;
  padding-left: 16px;
}

.subtopic {
  position: relative;
  z-index: 0;
  border-radius: 12px;
  overflow: hidden;
  margin-block: 12px;
  padding: 20px;
  padding-left: 60px;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid rgba(255, 255, 255, 0.06);
  transition: all 0.3s ease;
}

.subtopic:hover {
  background: rgba(255, 255, 255, 0.04);
}

.subtopic .number {
  position: absolute;
  left: 16px;
  top: 16px;
  font-size: 28px;
  font-weight: 800;
  color: rgba(168, 85, 247, 0.3);
}

.subtopic .detail {
  position: relative;
  flex: 1;
  max-width: 400px;
}

.subtopic .detail h3 {
  font-size: 15px;
  font-weight: 600;
  color: white;
  margin-bottom: 4px;
}

.subtopic .detail p {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.5);
  line-height: 1.5;
}

.subtopic .time {
  color: var(--clr);
  font-size: 12px;
  font-weight: 600;
  display: inline-block;
  margin-left: 8px;
  position: static;
  transform: none;
}

/* Buttons */
.subtopic .buttons {
  display: flex;
  gap: 8px;
  flex-shrink: 0;
}

.subtopic .buttons button {
  background: transparent;
  font-size: 12px;
  color: var(--clr);
  border: 1px solid var(--clr);
  padding: 8px 16px;
  width: auto;
  font-weight: 500;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.subtopic .buttons button:hover {
  background-color: var(--clr);
  color: #0d0d1a;
}

.quizButton {
  background-color: var(--clr) !important;
  color: #0d0d1a !important;
}

.quiz_completed {
  color: #10b981;
  padding: 8px 0;
  font-size: 11px;
  font-weight: 500;
}

.hardness {
  position: absolute;
  bottom: 8px;
  right: 16px;
  width: max-content;
  font-size: 11px;
  text-decoration: underline;
  cursor: pointer;
  opacity: 0.4;
  color: white;
  transition: opacity 0.3s ease;
}

.hardness:hover {
  opacity: 0.8;
}

/* Resources Modal */
.resources {
  height: 100%;
  margin: auto;
  gap: 24px;
  min-height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.resources button {
  position: relative;
  padding: 32px 24px;
  border-radius: 16px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  width: 220px;
  max-width: 90vw;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: rgba(255, 255, 255, 0.05);
  cursor: pointer;
  transition: all 0.3s ease;
}

.resources button.primary {
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border: none;
}

.resources button:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.resources .icon {
  width: 100%;
  margin-bottom: 12px;
  height: 48px;
}

/* AI Generated Content */
.res {
  line-height: 1.6;
  overflow: auto;
  margin-top: 24px;
  padding: 24px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: 16px;
}

.res p {
  margin-block: 16px;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
}

.res-heading {
  font-weight: 700;
  color: white;
  margin-bottom: 16px;
  font-size: 24px;
  line-height: 1.2;
}

.res h1, .res h2, .res h3 {
  color: white;
  margin-top: 24px;
  margin-bottom: 12px;
}

.res code {
  background: rgba(168, 85, 247, 0.2);
  padding: 2px 6px;
  border-radius: 4px;
  font-size: 13px;
}

.res pre {
  background: rgba(0, 0, 0, 0.3);
  padding: 16px;
  border-radius: 8px;
  overflow-x: auto;
}

.grecaptcha-badge {
  visibility: hidden !important;
}

/* Responsive */
@media (max-width: 768px) {
  .roadmap_wrapper .content {
    padding: 80px 16px 32px;
  }

  .topic-bar {
    padding: 20px;
    padding-top: 32px;
  }

  .topic-bar h2 {
    font-size: 16px;
  }

  .plus {
    right: 12px;
    top: 20px;
  }

  .plus svg {
    width: 28px;
    height: 28px;
  }

  .subtopic {
    padding: 16px;
    padding-left: 48px;
    flex-direction: column;
    align-items: flex-start;
    gap: 12px;
  }

  .subtopic .number {
    font-size: 22px;
    left: 12px;
    top: 12px;
  }

  .subtopic .detail {
    max-width: 100%;
  }

  .subtopic .buttons {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .subtopic .buttons button {
    flex: 1;
    min-width: 100px;
  }

  .resources {
    flex-direction: column;
  }

  .resources button {
    width: 100%;
  }
}
