/* Topic Page - Orbit Style */
.wrapper {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(180deg, #0d0d1a 0%, #1a1a2e 50%, #0d0d1a 100%);
  position: relative;
  overflow: hidden;
}

/* Decorative Background Elements */
.wrapper::before {
  content: '';
  position: absolute;
  width: 600px;
  height: 600px;
  border: 1px solid rgba(168, 85, 247, 0.1);
  border-radius: 50%;
  left: 50%;
  bottom: -300px;
  transform: translateX(-50%);
  pointer-events: none;
}

.wrapper::after {
  content: '';
  position: absolute;
  width: 900px;
  height: 900px;
  border: 1px solid rgba(99, 102, 241, 0.08);
  border-radius: 50%;
  left: 50%;
  bottom: -450px;
  transform: translateX(-50%);
  pointer-events: none;
}

.main {
  flex-direction: column;
  width: 100%;
  padding-top: 100px;
  min-height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 10;
}

h2 {
  color: #fff;
  font-size: clamp(20px, 4vw, 28px);
  font-weight: 500;
  text-align: center;
  margin-bottom: 8px;
}

h3 {
  font-weight: 400;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 16px;
}

/* Topic Input */
.TopicInput {
  margin-top: 24px;
  margin-bottom: 32px;
  position: relative;
  width: min(700px, 90vw);
}

.TopicInput input {
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(168, 85, 247, 0.3);
  padding: 16px 60px 16px 50px;
  font-size: clamp(24px, 5vw, 42px);
  font-weight: 500;
  color: white;
  outline: none;
  transition: border-color 0.3s;
}

.TopicInput input::placeholder {
  color: rgba(115, 115, 125, 0.7);
}

.TopicInput input:focus {
  border-bottom-color: #a855f7;
}

.TopicInput .icon {
  position: absolute;
  left: 0;
  bottom: 16px;
  color: rgba(168, 85, 247, 0.6);
  width: 40px;
  height: 40px;
}

.TopicInput button {
  position: absolute;
  right: 0;
  bottom: 12px;
  background-color: transparent;
  border: none;
  outline: none;
  transition: transform 0.3s ease;
  cursor: pointer;
}

.TopicInput button:hover {
  transform: scale(1.1);
}

.TopicInput button svg {
  width: 40px;
  height: 40px;
}

/* Suggestions */
.suggestions {
  width: min(800px, 95vw);
  padding: 16px;
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  justify-content: center;
}

.suggestions button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.suggestionPill {
  position: relative;
  background: linear-gradient(135deg, var(--clr) 0%, rgba(255, 255, 255, 0.05) 100%);
  color: white !important;
  padding: 12px 24px;
  padding-right: 50px;
  border-radius: 50px;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.suggestionPill .arrow {
  position: absolute;
  right: 16px;
  top: 50%;
  transform: translateY(-50%) translateX(10px);
  opacity: 0;
  transition: all 0.3s ease;
  width: 20px;
  height: 20px;
}

.suggestionPill:hover .arrow {
  transform: translateY(-50%) translateX(0);
  opacity: 1;
}

.suggestionPill:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
}

/* Set Details Section */
.setDetails {
  padding-top: 80px;
  gap: 8px;
}

.setDetails h2 {
  margin-bottom: 16px;
}

/* Input Container */
.inputContainer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.inputContainer input,
.inputContainer select {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px 24px;
  font-size: 16px;
  color: white;
  outline: none;
  transition: all 0.3s ease;
}

.inputContainer input:focus,
.inputContainer select:focus {
  border-color: #a855f7;
  background: rgba(168, 85, 247, 0.1);
}

.inputContainer select {
  cursor: pointer;
  min-width: 180px;
}

.inputContainer select option {
  background: #1a1a2e;
  color: white;
}

#timeInput {
  width: 100px;
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  color: #a855f7;
}

#timeUnit,
#knowledgeLevel {
  font-size: 28px;
  font-weight: 700;
  color: #a855f7;
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(168, 85, 247, 0.3);
  border-radius: 0;
  padding: 8px 16px;
}

.TimeInput {
  gap: 16px;
  margin-bottom: 32px;
}

/* Submit Button */
.SubmitButton {
  position: relative;
  font-size: 16px;
  font-weight: 600;
  padding: 16px 48px;
  border-radius: 12px;
  margin-top: 40px;
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 1;
  box-shadow: 0 10px 30px rgba(168, 85, 247, 0.3);
  overflow: hidden;
}

.SubmitButton:hover {
  transform: translateY(-3px);
  box-shadow: 0 15px 40px rgba(168, 85, 247, 0.4);
}

.SubmitButton::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, #c084fc 0%, #818cf8 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
}

.SubmitButton:hover::before {
  opacity: 1;
}

/* Remove old decorative elements */
.SubmitButton::after {
  display: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .main {
    padding-top: 80px;
    padding-left: 16px;
    padding-right: 16px;
  }

  h2 {
    font-size: 18px;
  }

  .TopicInput input {
    font-size: 24px;
    padding-left: 40px;
    padding-right: 50px;
  }

  .TopicInput .icon {
    width: 28px;
    height: 28px;
  }

  .TopicInput button svg {
    width: 28px;
    height: 28px;
  }

  .suggestionPill {
    font-size: 13px;
    padding: 10px 20px;
    padding-right: 40px;
  }

  #timeInput,
  #timeUnit,
  #knowledgeLevel {
    font-size: 20px;
  }

  .TimeInput {
    gap: 8px;
  }

  .SubmitButton {
    font-size: 14px;
    padding: 14px 36px;
    margin-top: 32px;
  }
}

@media (max-width: 480px) {
  .suggestions {
    gap: 8px;
  }

  .suggestionPill {
    font-size: 12px;
    padding: 8px 16px;
    padding-right: 32px;
  }

  .suggestionPill .arrow {
    width: 16px;
    height: 16px;
    right: 10px;
  }
}
