/* Header - Orbit Style */
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(13, 13, 26, 0.9);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  padding: 12px 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header .ProfileAvatar {
  display: none;
}

header .Home {
  position: absolute;
  right: 24px;
  top: 50%;
  transform: translateY(-50%);
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.3s ease;
  cursor: pointer;
}

header .Home:hover {
  color: #a855f7;
  transform: translateY(-50%) scale(1.1);
}

header h1 {
  font-size: 16px;
  font-weight: 400;
  margin-left: 0;
}

header h1 .x {
  font-weight: 400;
  font-size: 18px;
  font-family: inherit;
}

.logo-container {
  display: flex;
  align-items: center;
  gap: 8px;
  margin: 0;
}

.logo-icon {
  color: #a855f7;
  width: 24px;
  height: 24px;
}

.logo-text {
  font-size: 16px;
  font-weight: 600;
  color: white;
  letter-spacing: 2px;
}

.logo-highlight {
  background: linear-gradient(135deg, #a855f7 0%, #6366f1 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.logo {
  margin: 0;
}

/* Responsive */
@media (max-width: 768px) {
  header {
    padding: 10px 16px;
  }

  header .Home {
    right: 16px;
  }

  .logo-text {
    font-size: 14px;
  }

  .logo-icon {
    width: 20px;
    height: 20px;
  }
}
