/* Modal - Orbit Style */
.modal {
  position: fixed;
  inset: 0;
  z-index: 10000;
  background: rgba(13, 13, 26, 0.8);
  overflow: auto;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
}

.modal-content {
  position: relative;
  background: linear-gradient(180deg, #1a1a2e 0%, #0d0d1a 100%);
  border: 1px solid rgba(255, 255, 255, 0.1);
  max-width: 90vw;
  width: 700px;
  min-height: 350px;
  border-radius: 20px;
  padding: 32px;
  padding-top: 48px;
  line-height: 1.5;
  font-size: 14px;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.8);
  max-height: 90vh;
  overflow: auto;
}

button.cross {
  background: transparent;
  border: none;
  position: absolute;
  top: 16px;
  right: 16px;
  color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: color 0.3s ease;
}

button.cross:hover {
  color: white;
}

/* Responsive */
@media (max-width: 768px) {
  .modal {
    padding: 16px;
  }

  .modal-content {
    padding: 24px;
    padding-top: 40px;
    border-radius: 16px;
    min-height: 300px;
  }
}
